.header-container{
    width: 50%;
    margin: auto;
  
}
.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex: 1;
    margin: 6px 12px;
   
}
.header-logo{
    display: flex;
    align-items: center;
    font-size: 32px;
    font-weight: 500;
}
.header-logo i {
    display: flex;
    align-items: center;
    margin-left: 8px;
}
.header-button{
    font-size: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid white;
    border-radius: 6px;
    padding: 0px 8px;
}

@media only screen and (max-width:1024px){
    .header-conatainer{
        width: 90%;
    }
    .header-logo{
        font-size: 28px;
    }
    .header-button{
        font-size: 18px;
    }
}

@media only screen and (max-width:512px){
    .header-conatainer{
        width: 100%;
    }
    .header-logo{
        font-size: 24px;
    }
    .header-button{
        font-size: 16px;
    }
}